
//Slider
.slick-loading .slick-list {
  background: #fff url(#{$image-path}//ajax-loader.gif) center center no-repeat;
}

// Icons
@font-face {
  font-family: 'slick';
  font-weight: 400;
  font-style: normal;
  src: url(#{$font-path}/slick.eot);
  src: url(#{$font-path}/slick.eot?#iefix) format("embedded-opentype"),
         url(#{$font-path}/slick.woff) format("woff"),
         url(#{$font-path}/slick.ttf) format("truetype"),
         url(#{$font-path}/slick.svg#slick) format("svg");
}

// Arrows
.slick-prev,
.slick-next {
  font-size: 0;
  line-height: 0;
  position: absolute;
  top: 50%;
  display: none !important;
  width: 20px;
  height: 20px;
  padding: 0;
  -webkit-transform: translate(0,-50%);
  -ms-transform: translate(0,-50%);
  transform: translate(0,-50%);
  cursor: pointer;
  color: transparent;
  border: none;
  outline: none;
  background: transparent;
}

.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  color: transparent;
  outline: none;
  background: transparent;
}

.slick-prev:hover:before,
.slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: .25;
}

.slick-prev:before,
.slick-next:before {
  font-family: 'slick';
  font-size: 20px;
  line-height: 1;
  opacity: .75;
  color: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
  left: -25px;
}

[dir='rtl'] .slick-prev {
  right: -25px;
  left: auto;
}

.slick-prev:before {
  content: '←';
}

[dir='rtl'] .slick-prev:before {
  content: '→';
}

.slick-next {
  right: -25px;
}

[dir='rtl'] .slick-next {
  right: auto;
  left: -25px;
}

.slick-next:before {
  content: '→';
}

[dir='rtl'] .slick-next:before {
  content: '←';
}

// Dots
.slick-dotted.slick-slider {
  margin-bottom: 30px;
}

.slick-dots {
  position: absolute;
  bottom: 25px;
  display: block;
  width: 100%;
  padding: 0;
  margin: 0;
  list-style: none;
  text-align: center;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  padding: 0;
  cursor: pointer;
}

.slick-dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 20px;
  height: 20px;
  padding: 5px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent;

  &:hover,
  &:focus {
    outline: none;
  }
}

.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
  opacity: 1;
}

.slick-dots li button:before {
  font-family: 'slick';
  font-size: 6px;
  line-height: 9px;
  position: absolute;
  top: 0;
  left: 0;
  width: 10px;
  height: 10px;
  padding-left: 1px;
  border-radius: 50%;
  content: '•';
  text-align: center;
  opacity: 1;
  -webkit-font-smoothing: antialiased;
  transition: all .6s;
  -moz-osx-font-smoothing: grayscale;
  @media (max-width: $screen-sm-min){
    padding-left: 0;
  }
}

.slick-dots li.slick-active button:before {
  opacity: 1;
}








